.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 48px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;
    transition: border-bottom 0.3s;

    &:hover {
      border-bottom: 1px solid #fff;
    }
  }

  &__field {
    margin: 0 0 16px;
    font-family: inherit;

    &--last {
      margin-bottom: 32px;
    }
  }
}
