.menu {
  overflow-y: auto;
  background-color: $colorGray;

  &__content {
    height: 100vh;
    box-sizing: border-box;
    padding: $headerTopPadding 0;
  }

  &__top {
    margin-bottom: 44px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: 600;
    font-size: 22px;
    line-height: 22px;
    color: #1d1d1d;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.3s;

    &:hover {
      border-color: #1d1d1d;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 600;
    font-size: 16px;
    line-height: 27px;
    text-transform: uppercase;
    text-decoration: none;

    &--phone {
      transition: color 0.3s;
      margin-bottom: 16px;
      color: #1d1d1d;

      &:hover {
        color: #fff;
      }
    }

    &--action {
      color: #1d1d1d;
      transition: color 0.3s, border-bottom 0.3s;
      border-bottom: 1px solid #1d1d1d;
      padding-bottom: 8px;

      &:hover {
        color: #fff;
        border-bottom: 1px solid #fff;
      }
    }
  }
}
